import React, { useState, useEffect } from "react";
import PublicHeader from './header';
const axios = require("axios");

export const Login = (props) => {
    const [ users, setusers ] = useState([]);
    const [ username, setusername ] = useState('');
    const [ password, setpassword ] = useState('');
    const [ error, seterror ] = useState({});
    const { isLoggedin, setisLoggedin } = props.attributes;
    
    useEffect(() => {
        async function fetchData() {
            const url = 'fotostudio/api/users';
            let results = await axios.get( url )
            setusers( results.data );
        }
        fetchData();
    }, [] )

    const checkIfusernameExists = () => {
        let status = false;
        if( users ) {
            users.map( ( value, index ) => {
                if( value.username === username ) {
                    status =  true;
                }
            })
        }
        return status;
    }

    const checkIfpasswordExists = () => {
        let status = false;
        if( users ) {
            users.map( ( value, index ) => {
                if( value.username === username ) {
                    if( value.user_pass === password ) {
                        status =  true;
                    }
                }
            })
        }
        return status;
    }

    const onSubmit = (e) => {
        e.preventDefault();
        let errors = {};
        errors.error = false;

        if( !username && !password ) {
            errors.message = "Empty fields!";
            errors.error = true;
        }

        if( !username ) {
            errors.message = "Username empty!";
            errors.error = true;
        } else if( !checkIfusernameExists() ) {
            errors.message = "Username not exists";
            errors.error = true;
        }

        if( !password ) {
            errors.message = "Password empty!";
            errors.error = true;
        } else if( !checkIfpasswordExists() ) {
            errors.message = "Username or Password incorrect";
            errors.error = true;
        }
        
        seterror( errors );
        if( !errors.error ) {
            const url =   `fotostudio/api/session/true/${username}`;
            axios.get( url )
            .then(function(res) {
                setisLoggedin( res.data.isLoggedin )
            })
        }
    }

    const onLogout = () => {
        const url = `fotostudio/api/session/false/anonymous`;
        axios.get( url )
        .then(function(res) {
            setisLoggedin( res.data.isLoggedin );
        })
    }

    if( isLoggedin === "true") {
        return (
            <>
                <PublicHeader attributes = { props.attributes }/>
                You are logged in!!
                <button onClick= {(e) => onLogout() }>Logout</button>
            </>
        )
    }

    if( isLoggedin !== "true" ) {
        return (
            <>
            <PublicHeader attributes = { props.attributes }/>
                <form id="fotostudio-login-form">
                    <div className="fotostudio-login-form-wrapper">
                        <div className="input-wrapper">
                            <div className="form-heading single-field">
                                <h2>Already Have Account? <span>Login In!</span></h2>
                            </div>
                            { error.message &&
                                <span className="fotostudio-red-note">
                                    { error.message }
                                </span>
                            }
                            <div className="fotostudio-username single-field">
                                <label className="form-label" htmlFor="username">Username</label>
                                <input type="text" name="username" onChange={ (e) => setusername( e.target.value ) } placeholder="Enter username" value={ username }/>
                            </div>
                            <div className="fotostudio-password single-field">
                                <label className="form-label" htmlFor="password">Password</label>
                                <input type="password" name="password" onChange={ (e) => setpassword( e.target.value ) } placeholder="Enter password" value={ password }/>
                            </div>
                        </div>
                        <button type="submit" className="fotostudio-submit" onClick= { (e) => onSubmit(e) }>Log In</button>
                    </div>
                </form>
                <div className="fotostudio-form-postnotice">
                    Dont' have an account? Register <a href="/register">Go To Register</a>
                </div>
            </>
        );
    }
}