import React, { Component } from "react";

export default class PublicHeader extends Component {
    constructor( props ) {
        super( ...arguments );
    }

    render() {
        const { isLoggedin } = this.props.attributes
        return (
            <>
                <header id="header">
                    <h1 className="site-title">Fotostudio</h1>
                    <nav id="site-navigation">
                        <ul>
                            <li><a href="/">Home</a></li>
                            <li><a href="/trending">Trending</a></li>
                            <li><a href="/explore">Explore</a></li>
                            <li><a href="/brands">Brands</a></li>
                            <li><a href="/new-photos">New Photos</a></li>
                            <li><a href="/collections">Collections</a></li>
                            <li>
                                <a href="/login">
                                    { ( isLoggedin === 'true' ) &&
                                        'LogOut'
                                    }
                                    { ( isLoggedin !== 'true' ) &&
                                        'Login'
                                    }
                                </a>
                            </li>
                            { ( isLoggedin !== 'true' ) &&
                                <li><a href="/register">Join Us</a></li>
                            }
                            <li><a href="/submit-product">Submit Photos</a></li>
                        </ul>
                    </nav>
                </header>
            </>
        )
    }
}