import React, { useState } from "react";
import PublicHeader from './header';
const validator = require( "email-validator" );
const axios = require( "axios" );

export const Register = (props) => {
    const [ users, setusers ] = useState([]);
    const [ firstname, setfirstname ] = useState('');
    const [ middlename, setmiddlename ] = useState('');
    const [ lastname, setlastname ] = useState('');
    const [ username, setusername ] = useState('');
    const [ email, setemail ] = useState('');
    const [ password, setpassword ] = useState('');
    const [ confirmPassword, setconfirmPassword ] = useState('');
    const [ error, seterror ] = useState({});
    const [ success, setsuccess ] = useState({});

    const checkIfusernameExists = () => {
        async function fetchData() {
            const url = 'fotostudio/api/users';
            let results = await axios.get( url )
            setusers( results.data );
        }
        fetchData();

        let status = false;
        if( users ) {
            users.map( ( value, index ) => {
                if( value.username === username ) {
                    status =  true;
                }
            })
        }
        setusers( [] );
        return status;
    }

    const onSubmit = (e) => {
        e.preventDefault();
        let errors = {};
        errors.error = false;
        let successs = {};
        if( !firstname ) {
            errors.firstname = "Firstname is required";
            errors.error = true;
        }

        if( !lastname ) {
            errors.lastname = "Lastname is required";
            errors.error = true;
        }

        if( !username ) {
            errors.username = "Username is required";
            errors.error = true;
        } else if( username.length < 5 ) {
            errors.username = "Username must at least 5 letter";
            errors.error = true;
        } else if( username !== username.toLowerCase() ) {
            errors.username = "Username must be all lowercase";
            errors.error = true;
        } else if( checkIfusernameExists() ) {
            errors.username = "Username already taken";
            errors.error = true;
        }

        if( !email ) {
            errors.email = "Email is required";
            errors.error = true;
        } else if( !validator.validate(email) ) {
            errors.email = "Email is invalid";
            errors.error = true;
        }

        if( !password ) {
            errors.password = "Password is required";
            errors.error = true;
        } else if( password.length < 6 ) {
            errors.password = "Password must be at least 6 character";
            errors.error = true;
        }

        if( !confirmPassword ) {
            errors.confirmPassword = "Please Re enter password";
            errors.error = true;
        } else if( confirmPassword !== password ) {
            setconfirmPassword('');
            errors.confirmPassword = "Password must be same";
            errors.error = true;
        }

        seterror( errors );
        setsuccess( successs );
        if( !error.error ) {
            const url = `fotostudio/api/create-users`;
            axios.post( url, {
                username: username,
                password: password,
                firstname: firstname,
                middlename: middlename,
                lastname: lastname,
                email: email
            })
            .then(function(res) {
                if( res.data === "success" ) {
                    successs.message = "You have signed up succcessfully!"
                    setsuccess( successs );
                    setfirstname('');
                    setmiddlename('');  
                    setlastname('');
                    setusername('');
                    setemail('');
                    setpassword('');
                    setconfirmPassword('');
                    seterror(false);
                }
            })
        }
    }

    return (
        <>
            <PublicHeader attributes = { props.attributes }/>
            <form id="fotostudio-signup-form">
                <div className="fotostudio-signup-form-wrapper">
                    <div className="input-wrapper">
                        <div className="form-heading single-field">
                            <h2>Do Not Have Account? Please <span>Sign Up!</span></h2>
                        </div>
                        <div className="fotostudio-firstname single-field">
                            <label className="form-label" htmlFor="firstname">First Name</label>
                            <input type="text" id="firstname" name="firstname" placeholder="Enter your first name" onChange={ (e) => setfirstname( e.target.value) } value={firstname}/>
                            { error.firstname && 
                                <span className="fotostudio-red-note">{ error.firstname }</span>
                            }
                        </div>
                        <div className="fotostudio-middlename single-field">
                            <label className="form-label" htmlFor="middlename">Middle Name</label>
                            <input type="text" id="middlename" name="middlename" placeholder="Enter your middle name" onChange={ (e) => setmiddlename( e.target.value) } value={middlename}/>
                        </div>
                        <div className="fotostudio-lastname single-field">
                            <label className="form-label" htmlFor="lastname">Last Name</label>
                            <input type="text" id="lastname" name="lastname" placeholder="Enter your last name" onChange={ (e) => setlastname( e.target.value) } value={lastname}/>
                            { error.lastname && 
                                <span className="fotostudio-red-note">{ error.lastname }</span>
                            }
                        </div>
                        <div className="fotostudio-username single-field">
                            <label className="form-label" htmlFor="username">Username</label>
                            <input type="text" id="username" name="username" placeholder="Enter your username" onChange={ (e) => setusername( e.target.value) } value={username}/>
                            { error.username && 
                                <span className="fotostudio-red-note">{ error.username }</span>
                            }
                            { success.username && 
                                <span className="fotostudio-blue-note">{ success.username }</span>
                            }
                        </div>
                        <div className="fotostudio-email single-field">
                            <label className="form-label" htmlFor="email">Email Address</label>
                            <input type="text" id="email" name="email" placeholder="Enter your email address" onChange={ (e) => setemail( e.target.value) } value={email}/>
                            { error.email && 
                                <span className="fotostudio-red-note">{ error.email }</span>
                            }
                        </div>
                        <div className="fotostudio-password single-field">
                            <label className="form-label" htmlFor="password">Password</label>
                            <input type="password" id="password" name="password" placeholder="Enter your password" onChange={ (e) => setpassword( e.target.value) } value={password}/>
                            { error.password && 
                                <span className="fotostudio-red-note">{ error.password }</span>
                            }
                        </div>
                        <div className="fotostudio-password single-field">
                            <label className="form-label" htmlFor="confirmPassword">Confirm Password</label>
                            <input type="password" id="confirmPassword" name="confirmPassword" placeholder="Re-enter your password" onChange={ (e) => setconfirmPassword( e.target.value) } value={confirmPassword}/>
                            { error.confirmPassword && 
                                <span className="fotostudio-red-note">{ error.confirmPassword }</span>
                            }
                        </div>
                    </div>
                    <button type="submit" className="fotostudio-submit" onClick= { (e) => onSubmit(e) }>Sign Up</button>
                </div>
            </form>
            { success.message &&
                <div className="fotostudio-success-notice">
                    { success.message }
                </div>
            }
            <div className="fotostudio-form-postnotice">
                Already have an account? Login <a href="/login">Go To Login</a>
            </div>
        </>
    )
}